export const validateForm = {
    validateEmailForLogin(value) {
        let error;
        if (!value) {
            error = '\' ';
        } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(value)) {
            error = '    ';
        }
        return error;
    },
    validatePasswordForLogin(minLength, maxLength) {
        return (value) => {
            let error;
            if (!value) {
                error = '\' ';
            } else if (value.length > maxLength) {
                error = `     ${maxLength} `
            } else if (value.length < minLength) {
                error = `     ${minLength} `
            }
            return error;
        }
    }
}